		"blurb":  "In many ways, the central part of sports is the concept of team — a team’s chemistry, leadership, and culture often make the difference between success and failure. This was upended this past year, as close teams went virtual. Some successes were celebrated, from a historical look at the scrappy but close women’s soccer team to a men’s basketball team which overcame hurdles to say close. But teams can also be toxic, from a ‘lighter equals faster’ culture on Columbia track and field to a culture of sexual harassment and racism disguised as edgy humor within the marching band. The Spectator looked inside the good and the bad of team culture at Columbia this year.",
